.container {
  @apply bg-gray-100 mx-12 rounded-2xl mt-10 relative;
}

.header {
  @apply text-3xl text-primary-600 font-bold text-center pt-4;
}

.item {
  @apply h-auto flex items-start p-6 gap-10;
}

.title {
  @apply text-6xl pt-6 text-gray-500;
}

.content {
  @apply text-gray-500 text-3xl text-justify tracking-wide leading-relaxed pr-10 pb-10;
}

