.header {
  @apply text-4xl text-primary-600 font-bold mt-4 mb-2 mx-4;
}

.container {
  @apply m-4 pb-5 border-t-4 border-b-8 border border-primary-200 rounded-3xl shadow-lg;
}

.tabsContainer {
  @apply flex rounded-t-2xl overflow-hidden mb-3;
}

.tab {
  @apply px-10 py-5 text-gray-500 text-2xl flex-grow text-center
  border-b-4 border-b-gray-50;
}

.tab:hover {
  @apply bg-primary-50 cursor-pointer;
}

.tabSelected {
  @apply border-b-primary-200 border-b-4 bg-primary-50 shadow-lg;
}

.tabSelected:hover {
  @apply border-b-primary-200 bg-primary-50 cursor-default;
}

.listingsContainer {
  @apply flex flex-wrap justify-center;
}

.noDataContainer {
  @apply bg-red-200 my-10 py-10 rounded-full text-gray-600  text-center w-1/3 mx-auto;
}
