.container {
  @apply m-6 p-6 bg-white
  flex;
}

.header {
  @apply text-5xl text-primary-600 font-bold;
}

.text {
  @apply text-gray-500 text-3xl text-justify tracking-wide leading-relaxed pr-10 pb-10;
}

.button {
  @apply bg-primary-500 text-gray-100 text-xl
  my-2 mx-4 py-3 px-6
   rounded-2xl transition-all duration-300;
}

.button:hover {
  @apply bg-primary-600 rounded-xl;
}
