.container {
  @apply bg-white p-5 pb-12;
}

.header {
  @apply text-6xl text-primary-600 font-bold text-center;
}

.itemsContainer {
  @apply flex flex-wrap justify-center gap-3;
}

.item {
  @apply w-64 h-64 flex flex-col m-6 items-center text-center mb-8;
}

.icon {
  @apply p-3;
}

.title {
  @apply text-3xl text-gray-500;
}
