.container {
  max-width: 400px;
  @apply bg-gray-100 rounded-3xl shadow-md m-3 transition-all duration-300;
}

.container:hover {
  @apply cursor-pointer shadow-2xl scale-110;
}

.images {
  @apply overflow-hidden object-contain rounded-t-3xl;
}

.image {
  @apply object-cover;
  max-height: 180px;
}

.content {
  @apply p-5;
}

.title {
  @apply text-primary-600 text-xl text-center;
}

.subTitle {
  @apply text-gray-500 text-lg text-center;
}

.locationContainer {
  @apply flex justify-center text-xl gap-2 text-gray-500 py-1;
}

.detailsContainer {
  @apply flex justify-between items-center text-xl gap-2 text-gray-500;
}

.detailsItem {
  @apply flex justify-center items-end text-center text-sm gap-2 overflow-hidden;
}

.detailsItemText {
  @apply overflow-hidden whitespace-nowrap;
}
