.ceoContainer {
  @apply m-4 mt-8;
}

.ceoHeader {
  @apply text-6xl text-primary-600 font-bold;
}

.ceoMessage {
  @apply columns-lg text-lg;
}

.ceoFooter {
  @apply mt-4 flex items-center gap-4;
}

.ceoImage {
  @apply border-2 rounded-full w-20 h-20;
}

.ceoFooterText {
  @apply flex flex-col;
}

.ceoFooterTitle {
  @apply text-primary-500 text-lg;
}

.ceoFooterSubtitle {
  @apply text-primary-500 text-lg;
}
