.container {
  @apply m-4 mt-8;
}

.sectionTitle {
  @apply text-3xl text-primary-600 m-2;
}

.row {
  @apply flex gap-5;
}

.actions {
  @apply flex justify-start rounded-lg py-2 shadow-sm;
}

.action button {
  @apply px-20 py-5 bg-primary-400 text-primary-50 text-3xl uppercase mx-4 rounded-2xl transition-all duration-300;
}

.action button:hover {
  @apply border-primary-600  text-white rounded-3xl   bg-primary-500;
}

.action button:disabled {
  @apply text-gray-500 hover:border-gray-600  hover:bg-transparent;
}

.reset {
  @apply text-red-400;
}

.divider {
  @apply bg-gray-200 w-full my-3 mb-4;
  height: 1px;
}

.selectedImages {
  @apply h-20 w-20 object-contain mx-4
  flex gap-5;
}
