.overlay {
  @apply flex flex-col fixed inset-0 w-screen h-screen bg-black bg-opacity-50 z-overlay justify-center;
}

.dialog {
  @apply bg-gray-300  w-2/5
   /* w-96 min-w-fit max-w-full  */
  rounded-xl mx-auto shadow-lg;
}

.content {
  @apply py-4 px-3;
}

.title {
  @apply font-medium text-2xl text-gray-700 mb-2;
}

.description {
  @apply text-lg text-gray-800 mb-4;
}

.hr {
  @apply border-gray-400 mx-4;
}

.footer {
  @apply flex justify-end py-4 px-2;
}

.footer button {
  @apply text-xl font-medium w-fit mx-2 rounded-xl select-none py-3 px-4 uppercase;
}

.footer button:focus {
  @apply outline-none  ring-4 ring-opacity-50;
}

.normal {
  @apply text-gray-700;
}

.normal:hover {
  @apply text-gray-900;
}

.normal:focus {
  @apply ring-gray-600;
}

.primary {
  @apply bg-primary-400
  text-primary-800;
}

.primary:hover {
  @apply bg-primary-500 
  text-primary-900;
}

.primary:focus {
  @apply ring-primary-600;
}

.danger {
  @apply bg-red-300
  text-red-900;
}

.danger:hover {
  @apply bg-red-400 
  text-red-900;
}

.danger:focus {
  @apply ring-red-600;
}
