.item {
  @apply flex justify-between items-center
    m-2 p-3 py-5
     text-gray-600 border-2 rounded-lg;
}

.left {
  @apply flex justify-evenly items-center;
}

.right {
  @apply flex justify-evenly items-center gap-5;
}

.index {
  @apply rounded-full bg-primary-500 px-5 py-3 text-white text-center mr-4;
  min-width: 100px;
}

.subTitle {
  @apply opacity-70;
}

.item:hover {
  @apply bg-primary-50 cursor-pointer;
}

.action {
  @apply text-gray-500 cursor-pointer bg-primary-200 p-3 rounded-full transition-all duration-300;
}

.action:hover {
  @apply bg-primary-500 text-white cursor-pointer;
}
