.landingContainer {
  background-image: url('../../../../assets/images/landing.png');
  background-repeat: no-repeat;
  background-size: cover;
  @apply py-20;
}
.logo {
  max-width: 40%;
  @apply mx-auto;
}

.filtersContainer {
  max-width: 80%;
  @apply mx-auto mt-10 py-10 px-5 rounded-3xl;
  background-color: rgba(255, 255, 255, 0.7);
}

.filtersTitle {
  @apply text-primary-600 text-3xl;
}

.filters {
  @apply flex flex-wrap;
}

.buttonContainer {
  @apply flex;
}

.button {
  @apply bg-primary-500 text-gray-100 text-2xl
  my-2 mx-auto py-5 px-12
   rounded-2xl transition-all duration-300;
}

.button:hover {
  @apply bg-primary-600 rounded-xl;
}
