.container {
  @apply bg-primary-600 h-16 text-gray-200
  flex justify-around;
}

.item {
  @apply flex items-center;
}

.item:hover {
  @apply opacity-80 cursor-pointer;
}

.icon {
  @apply p-3;
}

.titlesContainer {
  @apply flex flex-col;
}

.title {
  @apply text-base;
}

.subTitle {
  @apply text-sm;
}
