.container {
  @apply mt-10;
}

.itemsContainer {
  @apply flex flex-wrap justify-center items-center;
}

.item {
  @apply flex flex-col bg-primary-200 m-4 p-8 rounded-2xl shadow-lg text-3xl gap-4 justify-center items-center transition-all duration-300 text-gray-500;
  min-width: 260px;
  min-height: 240px;
}

.item:hover {
  @apply bg-primary-300 shadow-xl rounded-3xl cursor-pointer;
}

.propertiesItem {
  @apply mx-auto  flex flex-col bg-primary-200 m-4 p-8 rounded-2xl shadow-lg text-3xl gap-4 justify-center items-center transition-all duration-300 text-gray-500;
  min-width: 460px;
  min-height: 200px;
  max-width: 460px;
  max-height: 200px;
}

.propertiesItem:hover {
  @apply bg-primary-300 shadow-xl rounded-3xl cursor-pointer;
}
