.container {
  @apply bg-gray-200 shadow-xl shadow-gray-300
  flex items-center px-6;
}

.navItem {
  @apply text-lg p-4;
}

.navItem:hover {
  @apply bg-gray-300  cursor-pointer;
}

.navItemSelected {
  @apply bg-yellow-500;
}

.logo {
  @apply w-14 h-14 p-2;
}

.flexSpacer {
  @apply grow;
}

.socialIcon {
  @apply text-primary-600 mx-2;
}

.socialIcon:hover {
  @apply text-primary-700 cursor-pointer;
}

.language {
  @apply bg-yellow-500;
}

.language:hover {
  @apply bg-yellow-500;
}
