.label {
  @apply mb-1 font-semibold text-lg text-gray-700;
}

.input,
.textarea {
  @apply border-2 border-gray-200 hover:border-gray-300 
  py-2 px-3 rounded-lg bg-gray-50 text-gray-600 outline-none text-xl;
}

.input:focus,
.textarea:focus {
  @apply border-2 border-gray-300 shadow-gray-200 shadow-sm;
}

.inputContainer {
  @apply flex flex-col mb-6 m-2 flex-grow;
  min-width: 420px;
  max-width: 620px;
}

.button {
  @apply block bg-primary-500 hover:bg-primary-600 text-white uppercase text-lg mx-auto p-4 rounded;
}

.error {
  @apply text-red-400 mt-1 text-lg;
}
