.react-select-container .react-select__control {
  @apply border-2  bg-transparent
  py-3 px-3 rounded-3xl text-xl;
}

.react-select-container .react-select__control--is-focused {
  @apply hover:border-primary-500
  border-2 border-primary-500;
}

.react-select-container .react-select__menu {
  @apply border-2 border-gray-200 rounded-3xl py-0 px-1 z-40;
}

.react-select-container .react-select__option {
  @apply text-gray-800  hover:bg-primary-50 py-4;
}
.react-select-container .react-select__option:first-child {
  @apply rounded-t-3xl mt-0;
}

.react-select-container .react-select__option:last-child {
  @apply rounded-b-3xl;
}

.react-select-container .react-select__option--is-focused {
  @apply bg-transparent;
}
.react-select-container .react-select__option--is-selected {
  @apply bg-primary-500;
}

.react-select-container .react-select__dropdown-indicator {
  @apply text-gray-800 hover:text-gray-500;
}

.react-select-container .react-select__clear-indicator {
  @apply text-gray-800 hover:text-gray-400;
}

.react-select-container .react-select__single-value {
  @apply text-gray-800;
}

.react-select-container .react-select__input-container,
.react-select-container .react-select__placeholder {
  @apply text-gray-800;
}

.react-select-container .react-select__indicator-separator {
  @apply bg-gray-500;
}
.react-select-container .react-select__indicator-separator {
  @apply bg-gray-500;
}
