.container {
  @apply m-4 text-2xl;
}

.form {
  @apply flex;
}

.hr {
  @apply mt-3 mb-6;
}

.addButton {
  @apply bg-primary-500 text-gray-100 
    my-2 mx-4 py-3 px-10 uppercase text-center
     rounded-2xl transition-all duration-300;
}

.addButton:hover {
  @apply bg-primary-600 rounded-xl;
}

.noDataContainer {
  @apply bg-red-200 my-10 py-10 rounded-full text-gray-600  text-center w-1/3 mx-auto;
}

.spaceH10 {
  @apply w-10;
}
